import React from "react"
import PropTypes from "prop-types"

import {
  CarouselProvider, Slider, Slide, Dot, ButtonBack, ButtonNext,
} from "pure-react-carousel"

import "pure-react-carousel/dist/react-carousel.es.css"

const inline = {
  style: {
    display: "inline",
    margin: "5px 10px",
  },
}

// eslint-disable-next-line react/prefer-stateless-function
class Carousel extends React.Component {
  static propTypes = {
    children: PropTypes.oneOfType([
      PropTypes.arrayOf(PropTypes.node),
      PropTypes.node,
    ]),
    naturalSlideWidth: PropTypes.number.isRequired,
    naturalSlideHeight: PropTypes.number.isRequired,
    style: PropTypes.objectOf(PropTypes.string),
    visibleSlides: PropTypes.number,
  }

  static defaultProps = {
    children: [],
    style: {},
    visibleSlides: 1,
  }

  static renderSlide(el, i) {
    return <Slide index={i} key={el.key}>{el}</Slide>
  }

  renderDot = (_, i) => {
    return <Dot
      {...inline}
      key={i}
      slide={i}
      className="atn-button atn-button_small"
    >
      {i + 1}
    </Dot>
  }

  renderControl = () => {
    const { children, visibleSlides } = this.props

    if (visibleSlides >= children.length) {
      return null
    }

    return <div style={{ display: "table", margin: "auto" }}>
      <ButtonBack {...inline} className="atn-button">Back</ButtonBack>
      {Array(children.length).fill().map(this.renderDot)}
      <ButtonNext {...inline} className="atn-button">Next</ButtonNext>
    </div>
  }

  render() {
    const { children, style, ...propRest } = this.props
    return <div style={style}>
      <CarouselProvider
        {...{ ...propRest, children: undefined }}
        totalSlides={children.length}
      >
        <Slider>
          {children.map(Carousel.renderSlide)}
        </Slider>
        <this.renderControl />
      </CarouselProvider>
    </div>
  }
}

export { Carousel }
